      flex: 0 1 2.5rem;
  background: none;
  outline: none;
  border: none;
  margin: 4px auto;
  padding: 0 10px;
  cursor: pointer;
  overflow: hidden;
  -webkit-transition: background 0.35s ease-in-out;
  -o-transition: background 0.35s ease-in-out;
  transition: background 0.35s ease-in-out;
}
.projects-container > button > img {
  position: relative;
  z-index: -1;
  max-width: 100%;
  opacity: 0.2;
  -webkit-transition: opacity 0.35s ease-in-out;
  -o-transition: opacity 0.35s ease-in-out;
  transition: opacity 0.35s ease-in-out;
}
.projects-container > button:hover > img {
  opacity: 0.6;
}
.projects-list {
  -ms-flex: 1 0;
      flex: 1 0;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  padding: 5px 0;
}
.projects-list > .project-item-container:first-child {
  margin-left: 0;
}
.projects-list > .project-item-container:last-child {
  margin-right: 0;
}
.project-item-container {
  -ms-flex: 0 0 25%;
      flex: 0 0 25%;
  margin: auto 5px;
  display: inline;
  -webkit-box-shadow: 0px 1px 3px rgba(0,0,0,0.4);
          box-shadow: 0px 1px 3px rgba(0,0,0,0.4);
  overflow: hidden;
  position: relative;
  cursor: pointer;
  -webkit-transition: all 0.35s ease-in-out;
  -o-transition: all 0.35s ease-in-out;
  transition: all 0.35s ease-in-out;
}
.project-item-container:hover .project-item {
  opacity: 0.6;
}
.project-item-container.selected {
  opacity: 0.3;
}
.project-item {
  background-size: cover;
  background-position: center;
  padding-bottom: 56.25%;
  -webkit-transition: all 0.35s ease-in-out;
  -o-transition: all 0.35s ease-in-out;
  transition: all 0.35s ease-in-out;
}
.project-shown {
  position: relative;
  -webkit-box-shadow: 0px 1px 3px rgba(0,0,0,0.4);
          box-shadow: 0px 1px 3px rgba(0,0,0,0.4);
}
.project-shown .container {
  background-size: cover;
  background-position: center;
  padding-bottom: 56.25%;
}
.project-shown.featured .project-details .title:after {
  content: 'ðŸ†';
  margin-left: 5px;
}
.project-details {
  padding: 1rem;
